@import url('https://fonts.googleapis.com/css2?family=Meddon&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pangolin&display=swap');
body{
  padding: 0;
  margin: 0;
  max-width: 1600px;
}
.App {
  font-family: 'Pangolin', cursive;
  display: grid;
  grid-template-areas:
  'header header'
  'navigator content'
  ;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  
}
.header{
grid-area: header;
background-color: rgb(69, 69, 145);
}
.navigator{
grid-area: navigator;
background-color: rgb(48, 48, 121);
color: cornsilk;
}
.content{
grid-area: content;
background-color: rgb(87, 87, 230);
}
.Setting{
  margin-top: 40px;
}
.navigator div a{
  margin: 30px;
  color: aliceblue;
  text-decoration: none;
}
.setting a{
padding-top: 40px;
}
.header span{
  font-family: 'Meddon', cursive;
  font-size: 30px;
}
.myProfileImg div{
  width: 300px;
}
.postMyProfile{
  background-color: aliceblue;
width: 300px;
border-radius: 10px;
}
.postMyProfileImg img{
  width: 40px;
  height: 40px;
  border-radius: 30px;
  object-fit: cover;

  
}
.addPost{
  padding: 10px;
}
.postMyProfile p{
  margin: 10px;
}
.masegePerson{
  display: grid;
  grid-template-columns: 2fr 10fr;
 
}
.navigator div  :hover {
color: aquamarine;
}
.navigator div a.active{
color: blue !important;
}

.personHere a{
 
  text-decoration: none;
  color: white;
}
.personHere a.active{
  color: blue !important;
}
  
  .personHere a:hover {
  color: aquamarine;
  }
.PersonHereIS{
  margin: 10px;
}
.close{
  cursor: pointer;
  float: right;
	opacity: 0.4;
	cursor: pointer;
  transition: opacity ease 0.4s;
}

.close:before,
.close:after {
  content: "";
  position: absolute;
  width: 32px;
  height: 2px;
  background: rgb(0, 0, 0);
  opacity: 0.4;
}

.close:before {
  transform: rotate(45deg);
}

.close:after {
  transform: rotate(-45deg);
}
.close:hover{
 opacity: 1;

}
